<script>
	// COMPONENTS
	import AppEditProfile from '@/components/Profile/AppEditProfile.vue';


	// SVG ICONS
	import { mdiMapMarker, mdiWeb, mdiCalendar, mdiPencil, mdiPencilOutline, mdiCameraRetakeOutline ,mdiEmail,mdiPhone,mdiBank,mdiChevronLeft,mdiChevronRight } from '@mdi/js';

	// VUEX
	import { mapGetters } from 'vuex';
	import { log } from 'util';
	import axios from 'axios';
	import Api from '../../service/Api';
	export default {
		components: {
			AppEditProfile,
		},
		props: {
			data: {
				type: Object,
			}
		},
		data() {
			return {
				cardLoading: false,
      			selection: 1,	
				
				assessments: [],
				
				svg: {
					location: mdiMapMarker,
					web: mdiWeb,
					calendar: mdiCalendar,
					pencil: mdiPencil,
					camera: mdiCameraRetakeOutline,
					email:mdiEmail,
					phone:mdiPhone,
					org:mdiBank,
					Lchev:mdiChevronLeft,
					Rchev:mdiChevronRight,


				}
			}
		},
		methods: {
			toAssessmentEdit (id) {
				this.cardLoading = true
				setTimeout(() => (this.$router.push({ name: 'assessmentEdit', params: {id: id} })), 2000)
			},
			toAssessmentReport (id) {
				this.cardLoading = true
				setTimeout(() => (this.$router.push({ name: 'assessmentReport', params: {id: id} })), 2000)
			},

			handleImageChange(event) {
			// const image = event.target.files[0];
				var f = event.target.files[0]; // FileList object
				var reader = new FileReader();
				var store =this.$store
				// Closure to capture the file information.
				reader.onload = (function(theFile) {
					return function(e) {
					var binaryData = e.target.result;
					//Converting Binary Data to base 64
					var base64String = window.btoa(binaryData);
					//Update the image
					const formData = {
						"image":base64String
					};
					//Upload the image
					store.dispatch('UPLOAD_IMAGE', formData);
					};
				})(f);
				// Read in the image file as a data URL.
				reader.readAsBinaryString(f);
			},
			editImage() {
				const imageInput = document.getElementById('imageInput');
				imageInput.click();
			},
			nextPage(){
				this.cardLoading = true;
				document.getElementById("btnPrev").classList.remove("v-btn--disabled")
				let vscope=this
				let page = this.assessmentsList._meta.currentPage+1
				
				if(this.assessmentsList._meta.currentPage<this.assessmentsList._meta.pageCount){
					const config = {
						headers: {
						"Authorization": localStorage.getItem('FBidToken'),
						}
					}
					Api().get('/assessments?page='+page,config)
					.then((res) => {
						vscope.$store.commit('SET_ASSESSMENTS_LIST', res.data)
						if(this.assessmentsList._meta.currentPage == this.assessmentsList._meta.pageCount){
							document.getElementById("btnNext").classList.add("v-btn--disabled")
						}
						this.cardLoading = false;
					})
				}else{
					document.getElementById("btnNext").classList.add("v-btn--disabled")
					this.cardLoading = false;

				}
				
			},
			prevPage(){
				this.cardLoading = true;

				let vscope=this
				let page = this.assessmentsList._meta.currentPage-1
				
				if(this.assessmentsList._meta.currentPage>1){
					const config = {
						headers: {
						"Authorization": localStorage.getItem('FBidToken'),
						}
					}
					Api().get('/assessments?page='+page,config)
					.then((res) => {
						vscope.$store.commit('SET_ASSESSMENTS_LIST', res.data)
						document.getElementById("btnNext").classList.remove("v-btn--disabled")
						if(this.assessmentsList._meta.currentPage == 1){
							document.getElementById("btnPrev").classList.add("v-btn--disabled")
							//document.getElementById("btnNext").classList.remove("v-btn--disabled")
						}
						this.cardLoading = false;

						
					})
				}else{
					document.getElementById("btnPrev").classList.add("v-btn--disabled")
					this.cardLoading = false;

				}
			}
		},
		computed: {
			...mapGetters(['isAuthenticated', 'userCredentials', 'theme', 'assessmentsList']),
			
		},
		mounted(){
			this.$store.dispatch('FETCH_ASSESSMENTS_LIST')
		},

	}
</script>
